<template>
  <div id="resume">
    <canvas v-for="item in totalPage" :key="item" :id="'the-canvas'+item" class="pdf-content"></canvas>
  </div>
</template>
<script>
// import HomeUrl from "@/components/HomeUrl.vue";
import PDFJS from "pdfjs-dist";
import pdfjsWorker from 'pdfjs-dist/build/pdf.worker.entry';

PDFJS.GlobalWorkerOptions.workerSrc = pdfjsWorker;

export default {
  name: "Resume",
  data: function() {
    return {
      pdfFile:'/简历.pdf',
      totalPage:1
    };
  },
  created: function() {
    let winW = document.documentElement.clientWidth;
    let loadingTask = PDFJS.getDocument(this.pdfFile);
    // eslint-disable-next-line
    var that=this;
    loadingTask.promise.then(
      function(pdf) {
        let pageNum = pdf.numPages;
        that.totalPage=pageNum;
        for (let i = 1; i <= pageNum; i++) {
          pdf.getPage(i).then(function(page) {
            let viewport = page.getViewport(1);
            let scale = (winW / viewport.width).toFixed(2);
            let scaledViewport = page.getViewport({ scale:scale});
            let canvas = document.getElementById("the-canvas" + i);
            let context = canvas.getContext("2d");
            canvas.height = scaledViewport.height;
            canvas.width = scaledViewport.width;
            let renderContext = {
              canvasContext: context,
              viewport: scaledViewport
            };
            let renderTask = page.render(renderContext);
            renderTask.then(function() {});
          });
        }
      },
      function(reason) {
        // console.error(reason);
        alert(reason)
      }
    );
  }
};
</script>

<style lang="less">
body {
  margin: 0;
  font-size: 10px;
  height: 100%;
  width: 100%;
}
html {
  width: 100%;
  height: 100%;
  // background-image: url("../../assets/bg1.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
#resume {
  height: 100%;
  width: 100%;
}
.pdf-content{
  display: block;
  margin: 0px auto;
}
</style>
