import Vue from 'vue'
import Router from 'vue-router'
import Resume from './Resume.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/resume',
      name: '首页',
      component: Resume
    },
    { path: '*', redirect: '/resume' }
  ]
})
